<template>
  <div id="login">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="main"></div>
          <div class="form">
            <h3 @click="showRegister">创建账户</h3>
            <transition name="fade">
              <div :class="{ show: isShowRegister }" class="register">
                <input
                  type="text"
                  placeholder="用户名"
                  v-model="register.username"
                />
                <input
                  type="password"
                  placeholder="密码"
                  v-model="register.password"
                  @keydown.enter="onRegister"
                />
                <p :class="{ error: register.isError }">
                  {{ register.notice }}
                </p>
                <div class="button" @click="onRegister">
                  创建账号
                </div>
              </div>
            </transition>

            <h3 @click="showLogin">登录</h3>
            <transition name="fade">
              <div :class="{ show: isShowLogin }" class="login">
                <input
                  type="text"
                  placeholder="输入用户名"
                  v-model="login.username"
                />
                <input
                  type="password"
                  placeholder="密码"
                  v-model="login.password"
                  @keydown.enter="onLogin"
                />
                <p :class="{ error: login.isError }">{{ login.notice }}</p>
                <div class="button" @click="onLogin">
                  登录
                </div>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Auth from "../apis/auth";
// import Bus from "../helpers/bus";
import { mapActions } from "vuex";

// Auth.getInfo().then(data => {
//   console.log(data);
// });

export default {
  name: "Login",

  data() {
    return {
      isShowLogin: false,
      isShowRegister: true,
      login: {
        username: "",
        password: "",
        notice: "请输入用户名和密码",
        isError: false
      },
      register: {
        username: "",
        password: "",
        notice: "创建账号后请记得自己的账号密码",
        isError: false
      }
    };
  },

  methods: {
    showLogin() {
      this.isShowLogin = true;
      this.isShowRegister = false;
    },
    showRegister() {
      this.isShowRegister = true;
      this.isShowLogin = false;
    },

    ...mapActions(["LoginUser", "RegisterUser"]),

    onRegister() {
      if (this.register.username == "" || this.register.password == "") {
        this.register.notice = "请输入用户名和密码";
        this.register.isError = true;
      } else if (!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
        this.register.notice = "用户名3~15个字符，仅限于字母数字下划线中文";
        this.register.isError = true;
      } else if (!/^.{6,16}$/.test(this.register.password)) {
        this.register.isError = true;
        this.register.notice = "密码长度为6~16个字符";
      } else {
        this.RegisterUser({
          username: this.register.username,
          password: this.register.password
        })
          .then(() => {
            this.register.isError = false;
            this.$router.push({ path: "/notebooks" });
            // Bus.$emit("sendUserMsg", { username: this.register.username });
          })
          .catch(err => {
            this.register.isError = true;
            this.register.notice = err.response.data.msg;
          });
      }
    },

    onLogin() {
      if (this.login.username == "" || this.login.password == "") {
        this.login.notice = "请输入用户名和密码";
        this.login.isError = true;
      } else if (!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {
        this.login.notice = "用户名3~15个字符，仅限于字母数字下划线中文";
        this.login.isError = true;
      } else if (!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true;
        this.login.notice = "密码长度为6~16个字符";
      } else {
        this.LoginUser({
          username: this.login.username,
          password: this.login.password
        })
          .then(() => {
            this.login.isError = false;
            this.login.notice = "";
            this.$router.push({ path: "/notebooks" });
            // Bus.$emit("sendUserMsg", { username: this.login.username });
          })
          .catch(err => {
            this.login.isError = true;
            this.login.notice = err.response.data.msg;
          });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.modal-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: table;
  transition: opacity 0.3s ease;
} //整个背景覆盖在最上层，点击其他按钮无效，实际是点击在背景上

.modal-wrapper {
  display: table-cell; //使用父元素display:table，子元素display:table-cell+vertical-align:middle的方式
  vertical-align: middle;
}

.modal-container {
  width: 800px;
  height: 500px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;

  .main {
    flex: 1;
    background: #2bb964
      url(https://cdn.pixabay.com/photo/2014/05/02/21/50/laptop-336378_960_720.jpg)
      center center no-repeat;
    background-size: contain;
  }
  .form {
    width: 270px;
    border-left: 1px solid #ccc;

    h3 {
      padding: 10px 20px;
      font-weight: normal;
      font-size: 16px;
      border-top: 1px solid #eee;
      cursor: pointer;

      &:nth-of-type(2) {
        border-bottom: 1px solid #eee;
      }
    }

    .button {
      background-color: #2bb964;
      height: 36px;
      line-height: 36px;
      text-align: center;
      font-weight: bold;
      color: #fff;
      border-radius: 4px;
      margin-top: 18px;
      cursor: pointer;
    }

    .login,
    .register {
      padding: 0px 20px;
      border-top: 1px solid #eee;
      height: 0;
      overflow: hidden;
      transition: height 0.5s;
      &.show {
        height: 193px; // 通过高度变化的过渡，没办法通过v-show
      }
      input {
        display: block;
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 6px;
        border-radius: 4px;
        border: 1px solid #ccc;
        outline: none;
        font-size: 14px;
        margin-top: 10px;
      }
      input:focus {
        border: 3px solid #9dcaf8;
      }

      p {
        font-size: 12px;
        margin-top: 10px;
        color: #444;
      }
      .error {
        color: red;
      }
    }
    .login {
      border-top: 0;
    }
  }
}
</style>
